html {
  margin: 8px;
  height: 100vh;
}

.principal {
  font-family: Arial, Helvetica, sans-serif;
}

.secondary {
  font-family: "Courier New", Courier, monospace;
  font-weight: 550;
}

/* @use './theme-blue.scss' as *; */
.box {
  height: 70px;
  width: 100px;
  padding: 16px;
  border: 1px solid rgba(190, 190, 190, 0.7960784314);
  gap: 1px;
  box-sizing: border-box;
  font-family: "Comic Sans MS", "Comic Sans", cursive;
  font-size: 13px;
  border-radius: 2px;
}

.base {
  background-color: red;
}

.important {
  background-color: #660000;
}

.warning {
  background-color: #ff9999;
}

.shadow {
  box-shadow: -10px 10px 12px #0b0b0b;
}